    border: 0;
    font-family: Arial, Helvetica, sans-serif;
} */
.AddTodoButton {
    background-color: #007bff; /* Колір фону кнопки */
    color: #fff; /* Колір тексту кнопки */
    padding: 10px 10px; /* Відступи всередині кнопки */
    /* width: 100%; */
    font-size: 16px; /* Розмір шрифту */
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    border: none; /* Видаляємо рамку */
    border-radius: 4px; /* Закруглені кути */
    cursor: pointer; /* Змінюємо курсор при наведенні */
    outline: none; /* Видаляємо обведення при фокусі */
    transition: background-color 0.2s ease-in-out; /* Плавна анімація зміни кольору фону */
    white-space: nowrap;
  }
  
  .button:hover {
    background-color: #0056b3; /* Колір фону кнопки при наведенні */
  }

.AddTodoInput {
    font-family: Arial, Helvetica, sans-serif;
    width: 100%; /* Ширина інпута на всю доступну ширину */
    height: 30px;
    padding: 2px;
    color: aliceblue;
    font-size: 16px; /* Розмір шрифту */
    border: 1px solid #0056b3; /* Тонка рамка */
    border-radius: 4px; /* Закруглені кути */
    outline: none; /* Видаляємо обведення при фокусі */
    transition: border-color 0.2s ease-in-out; /* Плавна анімація зміни кольору рамки */
    background: #4d556c61;
}

.AddTodoInput:focus {
    border-color: #007bff; /* Колір рамки при фокусі */
  }



  .AddTodoInputEmpty {
    position: absolute;
    bottom: 65%;
    left: 7%;
    color: azure;
    font-size: 12px;
  }